<template>
  <div id="testing">
    <v-container v-if="state == 'idle'" fluid class="cyan grey lighten-3">
      <v-layout row>
        <v-flex xs12 md12 class="text-xs-center">
          <div class="headline">Pengujian</div>
          <div class="body-1">Melakukan pengujian akurasi terhadap data.</div>
          <v-btn @click="runTesting" large dark color="blue">Jalankan</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container fluid v-if="state == 'done'">
      <v-layout row class="py-3 grey lighten-3">
        <v-flex md12 class="text-xs-center">
          <div class="headline text-xs-center">Hasil Pengujian</div>
          <div class="title text-xs-center">Akurasi: {{ accuracy.toFixed(3) }}%</div>
          <v-btn dark large depressed color="blue" @click="state = 'idle'">Ulangi</v-btn>
        </v-flex>
      </v-layout>
      <v-data-table
        :headers="tableHeaders"
        :items="tableItems"
      >
        <template v-slot:items="props">
          <tr v-bind:class="{ 'red lighten-3': props.item.testResult != 1 }">
            <td>{{ props.item.originalData.tanggal }}</td>
            <td>{{ props.item.originalData.suhu }}</td>
            <td>{{ props.item.originalData.tekananUdara }}</td>
            <td>{{ props.item.originalData.kecepatanAngin }}</td>
            <td>{{ props.item.originalData.kelembapan }}</td>
            <td>{{ props.item.originalData.keterangan }}</td>
            <td>{{ props.item.label }}</td>
          </tr>
        </template>
      </v-data-table>
    </v-container>
  </div>
</template>

<script>
import { Testing } from "@/services/testing";
import evbus from "@/evbus";
import tableHeaders from "./tableHeaders";

export default {
  name: "Testing",
  data: () => ({
    results: [],
    state: 'idle',
    accuracy: undefined,
    tableHeaders,
    tableItems: []
  }),
  methods: {
    async runTesting() {
      this.state = 'running';
      evbus.$emit('loading-on');
      try {
        const results = await Testing();
        let xs = results.filter(it => it.testResult === 1).length;
        let accuracy = 100.0 * (xs / results.length);
        this.accuracy = accuracy;
        this.tableItems = results;
        this.state = 'done';
      } catch (err) {
        this.state = 'idle';
        evbus.$emit('error', 'Gagal melakukan pengujian');
      } finally {
        evbus.$emit('loading-off');
      }
    }
  }
}
</script>

